@font-face {
  font-family: "Light";   /*Can be any text*/
  src: local("SketchBlockBold"),
    url("./SketchBlockBold.ttf") format("truetype");
}


body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
  background: url('./img/game-bg.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

#main .img-ruleta {
  width: 60%;
  height: auto;
  
	transition: all 4s ease;
	overflow-x: hidden
}

#main .img-responsive {
  width: 60%;
  height: auto;
}

#main .img-apuntador {
	display: inline-block;
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%, -50%);
}

.swal-title
{
  font-family: "Light";
  color:#5B108B;
  font-size: 11rem;
}

.swal-button
{
  background-color: #5B108B;
  font-size:2rem;
}

.swal-button:hover
{
  background-color: #5B108B !important;
}

.swal-button:focus
{
  background-color: #5B108B !important;
  border-color: #5B108B !important;
}

.swal-modal
{
  width: 1000px;
  height: 500px;
}
.swal-text
{
  font-size: 2rem;
}

.swal-footer{
  margin-top: 3rem;
  padding: 30px 20px;
}

.swal-title:not(:last-child)
{
  margin-bottom: 10px;
  margin-top: 20px;
}

.swal-button-container {
  margin: 18px 13px;
}

#btnAnimar {
  background:none;
  border:none; 
  padding:none;
}

@keyframes infinite-spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1100deg);
  }
}